.container {
  position: relative;
  width: 90%;
  max-width: 1256px;
  margin: 0 auto;
}

@font-face {
  font-family: 'Futura Book';
  src: url("../fonts/futura/FuturaBookC.woff2") format("woff2"), url("../fonts/futura/FuturaBookC.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: wrap;
}

@font-face {
  font-family: 'Futura Demi';
  src: url("../fonts/futura/FuturaDemiC.woff2") format("woff2"), url("../fonts/futura/FuturaDemiC.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: wrap;
}

@font-face {
  font-family: 'Futura Medium';
  src: url("../fonts/futura/FuturaMediumC.woff2") format("woff2"), url("../fonts/futura/FuturaMediumC.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: wrap;
}

/* Settings */
*, *::before, *::after {
  box-sizing: border-box;
}

body {
  font-family: 'Futura Book', sans-serif;
  font-size: 15px;
}

@media screen and (max-width: 575px) {
  body {
    font-size: 13px;
  }
}

mark {
  background: transparent;
  color: #E3B873;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Yeseva One', cursive;
  font-weight: normal;
}

img {
  width: 100%;
}

.dark-section {
  color: #FFFFFF;
}

.section {
  padding: 164px 0;
}

.section__heading {
  margin-bottom: 31px;
}

@media screen and (max-width: 575px) {
  .section__heading {
    margin-bottom: 40px;
  }
}

.section__subtitle {
  margin: 0 auto 47px;
  padding: 0 20px;
  font-size: 20px;
  text-align: center;
  line-height: 1.35;
  color: #363636;
}

@media screen and (max-width: 767px) {
  .section__subtitle {
    font-size: 18px;
  }
}

@media screen and (max-width: 575px) {
  .section__subtitle {
    font-size: 15px;
    margin-bottom: 36px;
  }
}

.section-heading {
  text-align: center;
}

.section-heading__title {
  margin: 0 0 13px;
  font-family: 'Yeseva One', cursive;
  font-size: 20px;
  text-transform: uppercase;
}

@media screen and (max-width: 575px) {
  .section-heading__title {
    font-size: 22px;
  }
}

.section-heading__img {
  position: relative;
  display: inline-block;
}

.section-heading__img::after, .section-heading__img::before {
  content: "";
  display: block;
  position: absolute;
  width: 32px;
  height: 2px;
  top: 50%;
  background: #E3B873;
}

.section-heading__img::after {
  left: -42px;
}

.section-heading__img::before {
  right: -42px;
}

.row {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}

@media screen and (max-width: 767px) {
  .row {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 575px) {
  .row {
    padding: 0;
  }
}

.col {
  flex-basis: 31.72%;
}

@media screen and (max-width: 767px) {
  .col {
    width: 80%;
  }
}

@media screen and (max-width: 575px) {
  .col {
    width: 100%;
  }
}

.card__img {
  position: relative;
  width: 100%;
  height: 29.24vw;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .card__img {
    height: 365px;
  }
}

.card__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.card__text {
  position: relative;
  width: 84%;
  margin: -127px auto 0;
  padding: 25px 8% 53px;
  background: #FFFFFF;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.03);
}

@media screen and (max-width: 1199px) {
  .card__text {
    font-size: 14px;
  }
}

@media screen and (max-width: 991px) {
  .card__text {
    width: 100%;
    margin-top: 0;
  }
}

@media screen and (max-width: 575px) {
  .card__text {
    width: 80%;
    margin-top: -141px;
    padding: 21px 9% 35px;
    margin-left: 0;
  }
}

.card__title {
  margin: 0 0 11px;
  font-size: 18px;
  color: #363636;
  white-space: nowrap;
}

@media screen and (max-width: 1199px) {
  .card__title {
    font-size: 16px;
  }
}

@media screen and (max-width: 991px) {
  .card__title {
    font-size: 15px;
  }
}

.card__list {
  margin: 0;
  padding-left: 15px;
  color: #959595;
}

@media screen and (max-width: 575px) {
  .card__list {
    font-size: 13px;
  }
}

.card__item {
  margin-bottom: 8px;
}

.card__label {
  position: absolute;
  bottom: 0;
  left: 0;
  min-width: 69%;
  height: 55px;
  padding: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Yeseva One', cursive;
  font-size: 18px;
  text-transform: uppercase;
  color: #FFFFFF;
  background: #E3B873;
  box-shadow: 5px 5px 10px rgba(227, 184, 115, 0.3);
  transform: translateY(50%);
}

@media screen and (max-width: 1199px) {
  .card__label {
    font-size: 16px;
  }
}

@media screen and (max-width: 575px) {
  .card__label {
    width: 71%;
    height: 40px;
    font-size: 15px;
  }
}

.form__input {
  padding: 6px 0;
  margin-bottom: 46px;
}

.form__input:last-of-type {
  margin-bottom: 0;
}

.form__policy {
  margin-top: 16px;
}

.label {
  display: block;
  font-size: 15px;
  color: rgba(54, 54, 54, 0.5);
}

.label_light {
  color: rgba(255, 255, 255, 0.5);
}

.input {
  background: transparent;
  border: none;
  font-family: 'Futura Medium';
  font-size: 18px;
  border-bottom: 1px solid rgba(54, 54, 54, 0.3);
}

.input.placeholder {
  opacity: 1;
  color: #363636;
}

.input:-moz-placeholder {
  opacity: 1;
  color: #363636;
}

.input::-moz-placeholder {
  opacity: 1;
  color: #363636;
}

.input:-ms-input-placeholder {
  opacity: 1;
  color: #363636;
}

.input::-webkit-input-placeholder {
  opacity: 1;
  color: #363636;
}

.input_light {
  color: #FFFFFF;
  border-bottom-color: rgba(255, 255, 255, 0.15);
}

.input_light.placeholder {
  opacity: 1;
  color: #FFFFFF;
}

.input_light:-moz-placeholder {
  opacity: 1;
  color: #FFFFFF;
}

.input_light::-moz-placeholder {
  opacity: 1;
  color: #FFFFFF;
}

.input_light:-ms-input-placeholder {
  opacity: 1;
  color: #FFFFFF;
}

.input_light::-webkit-input-placeholder {
  opacity: 1;
  color: #FFFFFF;
}

.policy__checkbox {
  display: none;
}

.policy__checkbox:checked + .policy__label > .checkbox-icon::before {
  display: block;
}

.policy__label {
  font-size: 16px;
  display: inline-flex;
  justify-content: flex-start;
  cursor: pointer;
}

.checkbox-icon {
  position: relative;
  display: block;
  margin-right: 9px;
  width: 18px;
  height: 18px;
  border-radius: 1px;
  background: #E3B873;
}

.checkbox-icon::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  background: url("../img/check.svg") no-repeat center;
  background-size: 80%;
}

/* Interface */
.btn {
  display: inline-block;
  padding: 12px 20px 11px;
  font-family: 'Futura Demi';
  font-size: 13px;
  text-transform: uppercase;
  text-decoration: none;
  background: #E3B873;
  color: #FFFFFF;
  box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.2);
  border-radius: 2px;
  border: none;
  cursor: pointer;
  transition: 0.3s;
}

.btn:hover {
  box-shadow: none;
}

.btn_outline {
  background: transparent;
  border: 1px solid #FFFFFF;
  box-shadow: none;
}

.btn_outline:hover {
  background: #131313;
}

.btn_big {
  padding: 17px 26px;
}

.btn_modal {
  padding: 19px 47px 17px;
}

@media screen and (max-width: 575px) {
  .btn {
    border-radius: 0;
  }
}

/* Header */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  padding: 21px 0;
  font-size: 14px;
  background: #131313;
}

@media screen and (max-width: 1199px) {
  .header {
    font-size: 12px;
  }
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-family: 'Futura Demi';
  text-transform: uppercase;
  color: #E3B873;
}

.nav__item {
  margin-right: 2.57vw;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFFFFF;
  transition: 0.3s;
}

.nav__item:hover {
  color: #E3B873;
}

.nav__item:last-child {
  margin-right: 0;
}

@media screen and (max-width: 767px) {
  .nav {
    display: none;
  }
}

.phone {
  font-family: 'Futura Demi';
  color: #E3B873;
}

.phone:hover {
  text-decoration: none;
}

.contacts__phone {
  margin-right: 45px;
}

@media screen and (max-width: 1199px) {
  .contacts__phone {
    margin-right: 20px;
  }
}

@media screen and (max-width: 991px) {
  .contacts__phone {
    margin-right: 0;
  }
}

@media screen and (max-width: 767px) {
  .contacts__phone {
    text-decoration: none;
    color: #FFFFFF;
  }
}

@media screen and (max-width: 991px) {
  .contacts__btn {
    display: none;
  }
}

/* Hero */
.hero {
  position: relative;
  padding-top: 83px;
  height: 100vh;
  max-height: 768px;
  background: #131313 url("../img/bg-hero.jpg") no-repeat center;
  background-size: cover;
}

.hero-content {
  max-width: 510px;
}

.hero-content__btn:first-child {
  margin-right: 20px;
}

@media screen and (max-width: 575px) {
  .hero-content__btn:first-child {
    margin-right: 0;
    margin-bottom: 26px;
  }
}

.hero-content__btn:last-child {
  flex-grow: 1;
}

.hero::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000;
  opacity: 0.7;
  z-index: 0;
}

.hero__instagram {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 97px;
  height: 100%;
  background: #131313;
}

@media screen and (max-width: 991px) {
  .hero__instagram {
    display: none;
  }
}

.hero__scroll-down {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 20;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 247px;
  height: 97px;
  font-family: 'Yeseva One', cursive;
  font-size: 12px;
  background: #131313;
  color: #FFFFFF;
  text-transform: uppercase;
  text-decoration: none;
  transform: translateY(50%);
}

.hero__scroll-down::after {
  content: "";
  display: "inline-block";
  position: "";
  width: 29px;
  height: 29px;
  margin-left: 18px;
  background: url("../img/arrow-down.svg") no-repeat center;
  border: 2px solid #E3B873;
  border-radius: 50%;
}

@media screen and (max-width: 767px) {
  .hero__scroll-down {
    display: none;
  }
}

.hero__text {
  margin: 18px 0 45px;
  font-size: 22px;
  line-height: 1.36;
}

@media screen and (max-width: 1199px) {
  .hero__text {
    font-size: 20px;
  }
}

@media screen and (max-width: 575px) {
  .hero__text {
    max-width: 321px;
    margin-top: 13px;
    margin-bottom: 32px;
    font-size: 18px;
  }
}

.hero__content {
  position: relative;
  z-index: 1;
  margin-left: 61px;
  margin-top: 13.125vw;
}

@media screen and (max-width: 575px) {
  .hero__content {
    margin: 58px auto 0;
  }
}

.hero__buttons {
  display: flex;
  justify-content: space-between;
  max-width: 435px;
}

@media screen and (max-width: 575px) {
  .hero__buttons {
    max-width: 340px;
    flex-direction: column;
  }
}

@media screen and (max-width: 575px) {
  .hero {
    height: 535px;
  }
}

.instagram__link {
  position: relative;
  display: block;
  font-family: 'Futura Demi';
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: #E3B873;
  transform: rotate(90deg);
}

.instagram__link::after {
  content: "";
  display: block;
  position: absolute;
  right: -65px;
  top: 52%;
  width: 50px;
  height: 2px;
  background: #E3B873;
}

.title {
  margin: 0;
  font-family: 'Yeseva One', cursive;
  font-size: 40px;
  line-height: 1.3;
  text-transform: uppercase;
}

@media screen and (max-width: 1199px) {
  .title {
    font-size: 36px;
  }
}

@media screen and (max-width: 575px) {
  .title {
    max-width: 300px;
    font-size: 23px;
  }
}

/* Control */
.control {
  background: #131313;
}

/* Types */
@media screen and (max-width: 767px) {
  .types__card {
    margin-bottom: 74px;
  }
}

/* Design */
.design-card {
  position: relative;
}

.design-card::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(19, 19, 19, 0.3);
}

.design__heading {
  margin-bottom: 18px;
}

@media screen and (max-width: 575px) {
  .design__heading {
    color: #FFFFFF;
  }
}

.design__subtitle {
  max-width: 785px;
  color: #363636;
}

@media screen and (max-width: 575px) {
  .design__subtitle {
    color: #FFFFFF;
  }
}

@media screen and (max-width: 767px) {
  .design__card {
    margin-bottom: 77px;
  }
}

@media screen and (max-width: 575px) {
  .design {
    background: #131313;
  }
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 506px;
  height: 509px;
  display: none;
  margin: auto;
  padding: 33px 3.4vw 27px;
  background: #131313 url("../img/bg-modal.svg") no-repeat center;
  background-size: cover;
  z-index: 199;
}

.modal_visible {
  display: block;
}

@media screen and (max-width: 575px) {
  .modal {
    width: 90%;
    padding-left: 5vw;
    padding-right: 5vw;
  }
}

.modal__close {
  position: absolute;
  top: 13px;
  right: 14px;
  width: 26px;
  height: 26px;
  padding: 0;
  background: transparent url("../img/close.svg") no-repeat center;
  background-size: cover;
  border: none;
  cursor: pointer;
}

.modal__title {
  max-width: 365px;
  margin: 0 auto 28px;
  font-size: 19px;
  line-height: 1.37;
  text-align: center;
}

@media screen and (max-width: 575px) {
  .modal__title {
    font-size: 16px;
  }
}

.modal__form {
  display: flex;
  flex-direction: column;
}

.modal__btn {
  align-self: flex-end;
  margin-top: 36px;
}

@media screen and (max-width: 575px) {
  .modal__btn {
    align-self: center;
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: none;
  background: rgba(19, 19, 19, 0.5);
  z-index: 100;
}

.overlay_visible {
  display: block;
}
